
#menu-container
   position: absolute
   left: 400px
   top: 32px
   padding: 0

@media only screen and (min-width:320px) and (max-width: 500px)
   #menu-container
      position: absolute
      left: 20px
      top: 62px

%a-hover
   background-color: #777
   border-top-left-radius: 8px
   border-top-right-radius: 8px
   
      
/* menu principale */

ul#menu 
   margin: 0px 0px 20px
   padding: 0px
   height: 30px
   width: 960px

   a,
   span
      color: white
      font-weight: bold
      text-decoration: none
      margin: 0

   li
      list-style-type: none
      margin: 0
      height: 30px
      float: left
      position: relative

   >li
      >a,
      >span
         font-size: 14px
         padding: 8px 12px
      >a:hover
         @extend %a-hover
      >span.active
         border-bottom: solid 4px red
         &:hover
            @extend %a-hover

   /* il menu di secondo livello Ã¨ visibile solo passando il mouse */

   li:hover
      >ul
         opacity: .75
         visibility: visible

   /* menu di secondo livello */

   ul
      margin: 0 0 20px
      padding: 0
      display: inline-block
      list-style: none
      opacity: 0
      visibility: hidden
      position: absolute
      top: 25px
      left: 0
      z-index: 9999
      width: 175px

      li
         width: 175px
         height: 23px
         padding: 4px 4px
         float: none
         display: block
         border: 0
         background-color: #444

         a,
         span.active
            font-size: .9em
            padding: 5px 8px
         a:hover
            background-color: none
         span.active 
            color: #fc0

         &:hover
            background: #888
         
